/* 动画1 */
.fly_updown{
animation: fly 3s linear infinite;/*infinite 循环*/
}
@keyframes fly{
    0%{
        top: 2%;
    }
    49%{
        top:15%;
    }
    100%{
        top: 2%;
    }
}
.fly_updown1{
    animation: fly1 1s linear infinite;/*infinite 循环*/
}
@keyframes fly1{
    0%{
        bottom: 335px;
    }
    49%{
        bottom: 355px;
    }
    100%{
        bottom: 335px;
    }
}
/* 云彩 */
@keyframes cloud {
    0%{
        left: 50%;
    }
    50%{
        left: -10%;
    }
    100%{
        left: 50%;
    }
}